  line-height: 45px;
  padding: 0 10px;
  float: left;
  margin-right: 10px;
  width: 180px;
}
.services__button {
  border: none;
  background: #385bae;
  color: #fff;
  font-size: 16px;
  line-height: 45px;
  text-align: center;
  padding: 0 25px;
  transition: opacity .3s;
  float: right;
}
.services__button:hover {
  opacity: .8;
}
.services__short {
  display: none;
}
.services__selected-avatar {
  position: absolute;
  left: 20px;
  right: 20px;
  top: 20px;
  bottom: 93px;
  z-index: 10;
}
.services__selected-img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.services__selected-controls {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 5;
}
.services__selected-controls:hover .services__selected-add {
  transform: translate(-50%, 0);
}
.services__selected-controls:hover .services__selected-remove {
  transform: translate(-50%, 0);
}
.services__selected-controls:hover .services__selected-close {
  transform: translateX(0);
}
.services__selected-text {
  line-height: 40px;
}
.services__selected-add {
  *zoom: 1;
  transition: opacity .3s, transform 0.3s;
  cursor: pointer;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 300px);
  bottom: 10px;
}
.services__selected-add:before,
.services__selected-add:after {
  content: "";
  display: table;
}
.services__selected-add:after {
  clear: both;
}
.services__selected-add:hover {
  opacity: .8;
}
.services__selected-add .icon {
  display: inline-block;
  background: url('../assets/img/plus.png');
  width: 41px;
  height: 40px;
  float: left;
}
.services__selected-add span {
  float: left;
  padding: 0 10px;
  background: #8FDE8D;
  display: inline-block;
  color: #fff;
}
.services__selected-remove {
  *zoom: 1;
  transition: opacity .3s, transform 0.3s;
  cursor: pointer;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 300px);
  bottom: 10px;
}
.services__selected-remove:before,
.services__selected-remove:after {
  content: "";
  display: table;
}
.services__selected-remove:after {
  clear: both;
}
.services__selected-remove:hover {
