  height: 40px;
  float: left;
}
.services__selected-remove span {
  float: left;
  padding: 0 10px;
  background: #8F8DDE;
  display: inline-block;
  color: #fff;
}
.services__selected-close {
  display: inline-block;
  transition: opacity .3s, transform 0.3s;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 10px;
  transform: translateX(300px);
}
.services__selected-close:hover {
  opacity: .8;
}
.services__selected-close .icon {
